export default {
	data() {
		return {
			questions: [],
		}
	},
	onLoad() {
		var arr = [
			{status:0},
			{status:1},
			{status:1},
			{status:0},
			{status:0},
			{status:1},
			{status:0},
		]
		this.questions = this.getNewList(arr)
	},
	methods: {
		toFeedback() {
			uni.navigateTo({
				url: '../feedback/feedback'
			})
		},
		getNewList(arr){
			var result = [];
			for(var i=0;i<arr.length;i+=2){
			    result.push(arr.slice(i,i+2));
			}
			return result			
		},
		toQuestionDetail(){
			uni.navigateTo({
				url:'../questionDetail/questionDetail'
			})
		}
	}
}
